import styled from "styled-components";

import { EngLoc } from "../../../assets/constants/localizations";

import { Title2 } from "../../../assets/styles/component";
import Slider from "../SliderDesktop";
import Toggler from "../TogglerDesktop";

import Emirates_logo from "../../../assets/images/Emirates_logo.svg";
import Huawei_logo from "../../../assets/images/Huawei_logo.svg";
import Hyundai_logo from "../../../assets/images/Hyundai_logo.svg";
import Mega_logo from "../../../assets/images/mega_logo.svg";
import Moscow24_logo from "../../../assets/images/Moscow24_logo.svg";
import Perekryostok_logo from "../../../assets/images/Perekryostok_logo.svg";
import Premier_logo from "../../../assets/images/premier_logo.svg";
import SberZvuk_logo from "../../../assets/images/SberZvuk_logo.svg";
import Sts_logo from "../../../assets/images/sts_logo.svg";
import Vkusvill_logo from "../../../assets/images/Vkusvill_logo.svg";
import X5Group_logo from "../../../assets/images/X5Group_logo.svg";
import OZON_logo from "../../../assets/images/OZON_logo.svg";
import { useEffect, useRef } from "react";
import { useState } from "react";
import { smooth_left } from "../../../assets/styles/animations";

const SPartnersInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 80px;
  margin: 0 auto;

  @media (max-width: 1200px) {
    margin: 0 5%;
  }

  & > div > div > div {
    opacity: 0;
    ${({ view }) => !view && smooth_left}
  }
`;

const PartnerLogo = styled.div`
  width: 380px;
  min-width: 380px;
  min-height: 250px;
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 28px;

  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;

  & img {
    min-width: clamp(25%, 6vw, 40%);
    max-width: clamp(70%, 6vw, 80%);
    max-height: clamp(45%, 6vw, 80%);
  }

  @media (max-width: 1400px) {
    width: 250px;
    min-width: 250px;
    min-height: 200px;
  }

  
`;

const LogosBox = styled.div`
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
`;

const PartnersInner = ({language}) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  const Logos1 = [
    Huawei_logo,
    Hyundai_logo,
    X5Group_logo,
    Premier_logo,
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
    Emirates_logo,
  ];
  const Logos2 = [
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
    Emirates_logo,
    Huawei_logo,
    Hyundai_logo,
    X5Group_logo,
    Premier_logo,
  ];
  const Logos3 = [
    Mega_logo,
    Moscow24_logo,
    SberZvuk_logo,
    Vkusvill_logo,
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
    Emirates_logo,
  ];

  const stepMargins = () => {
    let margin = 1620;
    let screenWidth = window.screen.width;

    if (screenWidth > 1750) margin = 400;
    if (screenWidth > 1400 && screenWidth < 1750) margin = 400;
    if (screenWidth < 1400) margin = 270;

    return margin;
  };

  const stepsCount = () => {
    let screenWidth = window.screen.width;
    let steps = 2;

    if (screenWidth > 1750) steps = 4;

    if (screenWidth > 1400 && screenWidth < 1750) steps = 5;

    if (screenWidth < 1400) steps = 5;

    return steps;
  };

  return (
    <SPartnersInner ref={elem} view={view}>
      <Title2>{language === "Ru" ? "С нами работают" : EngLoc.partners.partners_slider.title}</Title2>
      <Toggler language={language}></Toggler>
      <Slider stepMargin={stepMargins()} steps={stepsCount()}>
        <LogosBox>
          {Logos1.map((logo) => (
            <PartnerLogo key={Math.random()}>
              <img src={logo} loading="lazy" />
            </PartnerLogo>
          ))}
        </LogosBox>
        <LogosBox>
          {Logos2.map((logo) => (
            <PartnerLogo key={Math.random()}>
              <img src={logo} loading="lazy" />
            </PartnerLogo>
          ))}
        </LogosBox>
        <LogosBox>
          {Logos3.map((logo) => (
            <PartnerLogo key={Math.random()}>
              <img src={logo} loading="lazy" />
            </PartnerLogo>
          ))}
        </LogosBox>
      </Slider>
    </SPartnersInner>
  );
};

export default PartnersInner;
